module.exports=function(){$("[data-action=edit-profile]").click(function(){$(".profileWrapper").addClass("editing")}),$("[name=name1], [name=name2]").on("keydown",function(e){32==e.keyCode&&e.preventDefault()}),$(".autosize").autosize(),$("[data-action=save-profile]").click(function(){var e={bio:$("[name=bio]").val(),nome1:$("[name=name1]").val(),nome2:$("[name=name2]").val(),home:$("[name=home]").val(),location:$("[name=location]").val()};$.ajax({type:"PUT",dataType:"JSON",url:"/api/me/profile",data:{profile:e}}).done(function(e){if(e.error)e.message?app.flash.alert(e.message):console.warn("????",e);else if(e.data){var a=e.data;$(".profileWrapper").removeClass("editing"),$(".profileOutput.bio").html(a.profile.bio),$(".profileOutput.name").html(a.name),$(".profileOutput.home").html(a.profile.home),$(".profileOutput.location").html(a.profile.location)}else app.flash.alert("Um erro pode ter ocorrido.")}).fail(function(){})})};